import { addMessage, updateChat } from './api.js';

export async function handleSendMessage(senderId, receiverId, message) {
  try {
    // 1. Ajouter le message √† l'exp√©diteur
    await addMessage(senderId, message)
    
    // 2. Cr√©er une copie du message pour le destinataire
    const receiverMessage = {
      ...message,
      sent: false, // Pour le destinataire, c'est un message re√ßu
      senderId: senderId,
      receiverId: receiverId
    }
    
    // 3. Ajouter le message au destinataire
    await addMessage(receiverId, receiverMessage)
    
    // 4. Mettre √† jour le dernier message pour les deux utilisateurs
    const chatUpdate = {
      lastMessage: message.type === 'text' ? message.text : getMessagePreview(message),
      time: message.time,
      lastMessageTime: message.timestamp
    }
    
    await updateChat(senderId, chatUpdate)
    await updateChat(receiverId, chatUpdate)
    
    console.log("Message envoy√© avec succ√®s")
    return true
    
  } catch (error) {
    console.error("Erreur envoi message:", error)
    throw error
  }
}

function getMessagePreview(message) {
  switch (message.type) {
    case 'image':
      return 'üì∑ Photo'
    case 'video':
      return 'üé• Vid√©o'
    case 'audio':
      return 'üéµ Audio'
    case 'voice':
      return 'üé§ Message vocal'
    case 'document':
      return `üìé ${message.fileName}`
    default:
      return message.text
  }
}